import { _decorator, Component, instantiate, Prefab, Vec3 } from "cc";
import { Grid } from "./Grid";
const { ccclass, property } = _decorator;

@ccclass('GameArea')
export class GameArea extends Component {
    start() {
        this.createBlock();
    }

    @property({
        type: Prefab,
    })
    GridPrefab: Prefab;

    grids = [];

    public createBlock() {
        for (let i = 0; i < 64; i++) {
            let node = instantiate(this.GridPrefab);
            let x = (i % 8) * 80 - 320 + 40;
            let y = Math.floor(i / 8) * 80 - 320 + 40;
            node.setPosition(new Vec3(x, y, 0));
            this.node.getChildByName("Container").addChild(node);
            this.grids.push(this.node);
            this.node.getComponent(Grid).hide();
        }
    }
}


